import React from 'react'
import './issueSummary.styled.css'

const IssueSummary = ({issueList}) => {
    const completedCount = (list) => 
         list.filter(issue => issue.status === false).length
    
    const inprocessCount = (list) => 
         list.filter(issue => issue.status === true).length
    
    return (
        <div className="summary-container">
      
            <label>Mission Completed {completedCount(issueList)}</label>
            <label>In process {inprocessCount(issueList)}</label>
        </div>
    )
}

export default IssueSummary
